.Home {
    width: 100%;
    height: 100%;
    min-height: 450px;
    text-align: center;
    padding-top: 10%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 188, 212)), to(white));
    background: -webkit-linear-gradient(rgb(0, 188, 212), white);
    background: -o-linear-gradient(rgb(0, 188, 212), white);
    background: linear-gradient(rgb(0, 188, 212), white);
    text-transform: uppercase;
}


